"use client";

import React, { useEffect, useState } from 'react';
import Head from 'next/head';
import Navbar from "../navbar/page";
import { motion } from 'framer-motion';

interface LicenseData {
  license_status: string;
  license_number: string;
  active_date: string;
  period_of_validation: string;
  expires: string;
}

interface RoyaltyData {
  royalty_amount_due: number;
}

interface Announcement {
  id: number;
  content: string;
  created_at: string;
  // Add other fields as needed
}

export default function MinerDashboard() {
  const [isDarkMode, setIsDarkMode] = useState(true);
  const [language, setLanguage] = useState<'en' | 'si'>('en');
  const [licenseData, setLicenseData] = useState<LicenseData | null>(null);
  const [royaltyData, setRoyaltyData] = useState<RoyaltyData | null>(null);
  const [announcements, setAnnouncements] = useState<Announcement[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const handleThemeChange = (event: CustomEvent<{ isDarkMode: boolean }>) => {
      setIsDarkMode(event.detail.isDarkMode);
    };

    const handleLanguageChange = (event: CustomEvent<{ language: 'en' | 'si' }>) => {
      setLanguage(event.detail.language);
    };

    window.addEventListener('themeChange', handleThemeChange as EventListener);
    window.addEventListener('languageChange', handleLanguageChange as EventListener);

    // Set initial theme based on local storage or system preference
    const savedTheme = localStorage.getItem('theme');
    const systemPrefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    if (savedTheme === 'dark' || (!savedTheme && systemPrefersDark)) {
      setIsDarkMode(true);
    } else {
      setIsDarkMode(false);
    }

    // Set initial language based on local storage
    const savedLang = localStorage.getItem('language') as 'en' | 'si';
    if (savedLang === 'si') {
      setLanguage('si');
    } else {
      setLanguage('en');
    }

    return () => {
      window.removeEventListener('themeChange', handleThemeChange as EventListener);
      window.removeEventListener('languageChange', handleLanguageChange as EventListener);
    };
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        setError(null);

        // Fetch license data
        const licenseResponse = await fetch('https://ceylonminebackend.up.railway.app/miner/license');
        if (!licenseResponse.ok) throw new Error('Failed to fetch license data');
        const licenseData = await licenseResponse.json();
        setLicenseData(licenseData);

        // Fetch royalty data
        const royaltyResponse = await fetch('https://ceylonminebackend.up.railway.app/miner/royalty');
        if (!royaltyResponse.ok) throw new Error('Failed to fetch royalty data');
        const royaltyData = await royaltyResponse.json();
        setRoyaltyData(royaltyData);

        // Fetch announcements
        const announcementsResponse = await fetch('https://ceylonminebackend.up.railway.app/miner/announcements');
        if (!announcementsResponse.ok) throw new Error('Failed to fetch announcements');
        const announcementsData = await announcementsResponse.json();
        setAnnouncements(announcementsData);

      } catch (err) {
        setError(err instanceof Error ? err.message : 'An error occurred');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const translations = {
    en: {
      title: "Miner Dashboard",
      licenseStatus: "License Status",
      licenseNumber: "License Number",
      activeDate: "Active Date",
      periodOfValidation: "Period of Validation",
      expiresOn: "Expires On",
      royaltyDue: "Royalty Amount Due",
      announcements: "Recent Announcements",
      loading: "Loading...",
      error: "Error loading data"
    },
    si: {
      title: "à¶´à¶­à¶½à·Š à¶šà¶¸à·Šà¶šà¶»à·” à¶‹à¶´à¶šà¶»à¶« à¶´à·”à·€à¶»à·”à·€",
      licenseStatus: "à¶¶à¶½à¶´à¶­à·Šâ€à¶» à¶­à¶­à·Šà·€à¶º",
      licenseNumber: "à¶¶à¶½à¶´à¶­à·Šâ€à¶» à¶…à¶‚à¶šà¶º",
      activeDate: "à¶šà·Šâ€à¶»à·’à¶ºà·à¶­à·Šà¶¸à¶š à¶¯à·’à¶±à¶º",
      periodOfValidation: "à·€à¶½à¶‚à¶œà·” à¶šà·à¶½ à·ƒà·“à¶¸à·à·€",
      expiresOn: "à¶šà¶½à·Š à¶‰à¶šà·”à¶­à·Š à·€à¶± à¶¯à·’à¶±à¶º",
      royaltyDue: "à¶œà·™à·€à·’à¶º à¶ºà·”à¶­à·” à¶»à·à¶¢à·à¶°à·’à¶»à·à¶¢à·Šâ€à¶º à¶¸à·”à¶¯à¶½à·Š",
      announcements: "à¶¸à·‘à¶­ à¶šà·à¶½à·“à¶± à¶±à·’à·€à·šà¶¯à¶±",
      loading: "à¶´à·–à¶»à¶«à¶º à·€à·™à¶¸à·’à¶±à·Š...",
      error: "à¶¯à¶­à·Šà¶­ à¶´à·–à¶»à¶«à¶º à¶šà·’à¶»à·“à¶¸à·š à¶¯à·à·‚à¶ºà¶šà·Š"
    }
  };

  const t = translations[language];

  return (
    <div
      className={`relative min-h-screen ${
        isDarkMode ? 'bg-black text-white' : 'bg-gray-50 text-gray-900'
      }`}
    >
      <Head>
        <title>Miner Dashboard | CeylonMine</title>
        <meta
          name="description"
          content="CeylonMine Miner Dashboard - View your license status, royalty information, and announcements."
        />
        <link rel="icon" href="/favicon.ico" />
      </Head>

      <Navbar />

      <main className="relative z-10 pt-32 pb-16">
        <div className="container mx-auto px-4">
          <motion.h1
            className="text-4xl md:text-6xl font-bold mb-12 text-center"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
          >
            {t.title}
          </motion.h1>

          {loading ? (
            <div className="text-center text-xl">{t.loading}</div>
          ) : error ? (
            <div className="text-center text-red-500 text-xl">{t.error}</div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {/* License Information Card */}
              <motion.div
                className={`rounded-xl p-6 ${
                  isDarkMode ? 'bg-gray-900 bg-opacity-70' : 'bg-white shadow-lg'
                }`}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6 }}
              >
                <h2 className="text-2xl font-bold mb-6">{t.licenseStatus}</h2>
                {licenseData && (
                  <div className="space-y-4">
                    <div>
                      <p className="text-sm opacity-70">{t.licenseNumber}</p>
                      <p className="text-lg font-medium">{licenseData.license_number}</p>
                    </div>
                    <div>
                      <p className="text-sm opacity-70">{t.activeDate}</p>
                      <p className="text-lg font-medium">{licenseData.active_date}</p>
                    </div>
                    <div>
                      <p className="text-sm opacity-70">{t.periodOfValidation}</p>
                      <p className="text-lg font-medium">{licenseData.period_of_validation}</p>
                    </div>
                    <div>
                      <p className="text-sm opacity-70">{t.expiresOn}</p>
                      <p className="text-lg font-medium">{licenseData.expires}</p>
                    </div>
                  </div>
                )}
              </motion.div>

              {/* Royalty Information Card */}
              <motion.div
                className={`rounded-xl p-6 ${
                  isDarkMode ? 'bg-gray-900 bg-opacity-70' : 'bg-white shadow-lg'
                }`}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.2 }}
              >
                <h2 className="text-2xl font-bold mb-6">{t.royaltyDue}</h2>
                {royaltyData && (
                  <div className="text-4xl font-bold text-orange-500">
                    Rs. {royaltyData.royalty_amount_due.toLocaleString()}
                  </div>
                )}
              </motion.div>

              {/* Announcements Card */}
              <motion.div
                className={`rounded-xl p-6 ${
                  isDarkMode ? 'bg-gray-900 bg-opacity-70' : 'bg-white shadow-lg'
                }`}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.4 }}
              >
                <h2 className="text-2xl font-bold mb-6">{t.announcements}</h2>
                <div className="space-y-4">
                  {announcements.map((announcement) => (
                    <div
                      key={announcement.id}
                      className={`p-4 rounded-lg ${
                        isDarkMode ? 'bg-gray-800' : 'bg-gray-50'
                      }`}
                    >
                      <p className="text-sm opacity-70">
                        {new Date(announcement.created_at).toLocaleDateString()}
                      </p>
                      <p className="mt-2">{announcement.content}</p>
                    </div>
                  ))}
                </div>
              </motion.div>
            </div>
          )}
        </div>
      </main>
    </div>
  );
} 